.container {
  justify-content: center;
  margin-top: 50px;
  display: flex;
}

.clock {
  background-color: #dcdcdc;
  border: 6px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.clock__seconds {
  transform-origin: 50% 100%;
  background-color: green;
  width: 10px;
  height: 50%;
  animation: 5s linear infinite move;
  position: absolute;
  top: 0;
}

.clock__minutes {
  transform-origin: 0;
  background-color: #00f;
  width: 25%;
  height: 20px;
  animation: 60s linear infinite move;
  position: absolute;
  left: 50%;
}

.clock__center {
  background-color: orange;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  position: absolute;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

